import { Node } from '@ali/4ever-cangjie';

export enum UploadErrorType {
  oversizeError = 'oversize_error',
  networkError = 'network_error',
}

export type EmbedNode = Node & {
  data: {
    src: string;
    viewType: 'preview' | 'card',
    name: string;
    type: string;
    size: number;
    previewSize: number;
    uploadPercent?: number;
    errorType?: UploadErrorType;
    poster?: string;
  }
};

export interface VideoErrorMessage {
  isError?: boolean;
  errorSrc?: string;
}

export interface VideoMaskParams {
  node: EmbedNode,
  canPlay: boolean,
  reload: Function,
  errorMessage?: VideoErrorMessage,
}
